*,
*::after,
*::before{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

html{
  font-size: 62.5%;
  font-family: 'Open Sans', sans-serif;
}

body{
  font-family: inherit;
}

:root{
  --color-white: #fff;
  --color-light-white: #eee;

  --color-tomato: #d40640;
  --color-purple-transparent: rgba(88,24, 69, .9);
  --color-pink-transparent: rgba(216,14,91, .9);

  --color-orange: #ff3c00;
  --color-light-black: #222;
  --color-black: #000;
  --color-slate-gray: #778899;

  --shadow:(0,0,0, .4); 
}


.app {
    height: 100vh;
    width: 100%;
    background-image: linear-gradient(to bottom right, var(--color-pink-transparent), var(--color-purple-transparent)), url(https://i.pinimg.com/originals/91/b5/cd/91b5cdab51e207263169904b227503b4.jpg);
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
  }

.calculator {
    height: 80vh;
    width: 25%;
    box-shadow: 0 2rem 4rem var(--shadow);
    background-color: var(--color-light-black);
    background-color: var(--color-white);
  }

  .result-screen {
    height: 19vh;
    font-size: 8rem;
    font-weight: 600;
    /* To display the value at the bottom-right of the container */
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
  }

  .computation-screen {
    height: 6vh;
    font-size: 2.5rem;
    
    /* To display the value at the bottom-right of the container */
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
  }

  .screen {
    height: 25vh;
    width: 100%;
    padding: 0 2rem;
    overflow: hidden;
    white-space: no-wrap;
    color: var(--color-light-white);
    background-color: var(--color-tomato);
  }

  .keypad__row {
    height: 11vh; /* (80vh calculator - 25vh screen) / 5 keypadRow components = 11vh */
    width: 100%;
    display: flex; /* To control the dimension */
    color: white;
  }

  .keypad {
    height: 55vh;
    width: 100%;
  }

  .btn {
    width: 25%;
    height: 100%;
    font-size: 3rem;
    font-family: inherit;
    background-color: var(--color-white);
    border: 1px solid transparent;
    outline: none;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .2s;
  }
  .btn:hover {
    color: var(--color-light-white);
    background-color: var(--color-tomato);
  }
  .btn:active {
    transform: scale(.95);
  }

  .btn--large {
    width: 50%;
    color: var(--color-white);
    background-color: var(--color-tomato);
    transition: all .2s;
  }
  .btn--large:hover {
    background-color: var(--color-white);
    color: var(--color-tomato);
  }

  :root {
    --color-orange: #ff3c00;
  }
  .btn--primary {
    color: var(--color-tomato);
  }
  .btn--operator {
    color: var(--color-orange);
  }

  .results-table {
    height: 80vh;
    width: 25%;
    box-shadow: 0 2rem 4rem var(--shadow);
    background-color: var(--color-slate-gray);
    float: right;
    margin: 50px;
    color: var(--color-white);
    font-size:24px;
    font-weight: bolder;
  }

  .table-item{
    background-color: var(--color-light-white);
    color: var(--color-light-black);
    height: 70px;
    font-size: 24px;
    padding: 10px;
   
  }

  .table-item:hover{
    background-color: var(--color-tomato);
    color: var(--color-light-white)
  }